.buttons {
  display: flex !important;
  padding: 0;
  margin: 20px 0;
}
.buttons li {
  width: 25%;
  list-style: none;
  text-align: center;
}
.buttons li > span {
  height: 81px;
  display: block;
}
.buttons li:nth-child(2) {
  padding-top: 5px;
}
.buttons li:nth-child(2) span + img {
  margin-top: -9px;
}
.buttons img {
  display: inline-block;
  opacity: 0.4;
}
.buttons li.chosen img {
  opacity: 1;
}

@media only screen and (min-width: 992px) {
  .sidebar {
    width: 380px;
    height: calc(100vh - 40px);
    overflow: scroll;
  }
  .bm-cross-button {
    display: none;
  }
}
@media only screen and (max-width: 991px) {
  .sidebar {
    margin: 0 auto;
  }
  .sidebar h1 {
    max-width: 380px;
    margin: 0 auto;
  }
  .adp {
    display: none;
  }
}
@media only screen and (max-width: 575px) {
  .sidebar h1 {
    background-position: 111% 80% !important;
    background-size: 50%;
  }
  .sidebar h1 img {
    width: 100%;
  }
}
@media only screen and (max-width: 350px) {
  .buttons li:nth-child(1) span img {
    width: 100%;
  }
  .buttons li:nth-child(2) span img {
    width: 83%;
  }
  .buttons li:nth-child(3) span img {
    width: 92%;
  }
  .buttons li:nth-child(4) span img {
    width: 92%;
  }
  .button span {
    height: 90%;
  }
  .buttons span + img {
    width: 90%;
  }
}

.sidebar {
  padding: 20px;
}
.sidebar h1 {
  padding-top: 20px;
  margin-bottom: 30px;
  background-image: url(/footprint-calculator/static/media/title_img.563016f2.png);
  background-repeat: no-repeat;
  background-position: 185px -40px;
}
.sidebar label {
  display: inline-block;
  width: 40px;
  height: calc(34px - 7px);
  border-radius: 6px 0 0 6px;
  text-align: center;
  vertical-align: top;
  padding-top: 7px;
  color: #4d4d4d;
  background-color: #add3b3;
}
.sidebar input {
  display: inline-block;
  width: calc(100% - 54px);
  border-radius: 0 6px 6px 0;
  box-shadow: none;
  border: 1px #b7b7b7 solid;
  border-left: none;
  margin-bottom: 10px;
  padding: 5px;
}
.sidebar #option {
  width: 100%;
  margin-bottom: 10px;
}
.sidebar #time {
  width: 49%;
  margin: 0 2% 10px 0;
}
.sidebar #date {
  width: 49%;
  margin-bottom: 10px;
}

/* Map results */
.sidebar .adp-listheader {
  display: none;
}
.sidebar .adp-list {
  padding: 0;
  border: none;
}
.sidebar .adp-list ol {
  overflow: hidden;
  border: none !important;
}
.sidebar .adp-list ol li {
  margin-bottom: 10px;
  background-color: #e6e7e8;
  /* background-color: #fff; */
  padding: 10px !important;
}
.adp-warnbox {
  display: none;
}
.adp {
  background-color: #fff;
}
.adp-agencies {
  display: none;
}
.adp-legal {
  display: none;
}

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 36px;
  top: 36px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #373a47;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #a90000;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100%;
}

/* General sidebar styles */
.bm-menu {
  /* background: #373a47; */
  /* background: lightgrey; */
  /* padding: 2.5em 1.5em 0; */
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em;
}

/* Individual item */
.bm-item {
  display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

.buttons {
  display: flex !important;
  padding: 0;
}
.buttons li {
  width: 25%;
  list-style: none;
}

