.trivia {
  width: 400px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  border-radius: 30px;
  border: solid 1px green;
  background-color: #fff;
  padding: 30px;
  background-image: url(/footprint-calculator/static/media/trivia.80839407.png);
  background-repeat: no-repeat;
  background-position: 320px 10px;
}
.trivia h2 {
  color: green;
  font-weight: bold;
  font-size: 1.1em;
  margin-bottom: 10px;
}
.trivia a {
  color: green;
}
.trivia .trivia_container {
  height: 80px;
  width: 290px;
}
.trivia .emission {
  font-weight: bold;
  font-size: 1.2em;
}

@media only screen and (min-width: 992px) {
  .trivia {
    position: fixed;
    top: 20px;
    right: 60px;
  }
}
@media only screen and (max-width: 991px) {
  .trivia .emission {
    display: none;
  }
  .trivia {
    margin: 40px auto;
  }
}
@media only screen and (max-width: 575px) {
  #trivia {
    padding: 0 10px;
  }
  .trivia {
    width: 75%;
    background-position: right 10px;
  }
  .trivia_container {
    width: calc(100% - 100px) !important;
    height: -webkit-fit-content !important;
    height: -moz-fit-content !important;
    height: fit-content !important;
  }
}

html {
  font-family: Roboto, Arial, sans-serif;
}
.map {
  height: 100vh;
  overflow: hidden;
}
@media only screen and (min-width: 992px) {
  .container {
    display: flex;
  }
  .map_emission {
    display: none;
  }
  .map > div > div {
    width: calc(100% - 420px) !important;
  }
}
@media only screen and (max-width: 991px) {
  .map_emission {
    background-color: #00bd3e;
    color: #fff;
    padding: 5px 0;
    text-align: center;
    font-weight: bold;
  }
}

